
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import axios from 'axios'
import * as util from '../api/common'

export default {
components: {
Cell, Group, Tab, TabItem
},
data () {
return {
detail: {
id: 9,
order_sn: '180830113230002009',     // 账单编号
buyer_id: 1,
amortize_time: 1,                      // 分期
month_account: 1674.41,                 // 应还款金额
refund_account: 1674.41,             // 实际还款金额
over_cover_charse: 0,
cover_charse: 7.75,               // 服务费
status: 1,
end_time: 1538236800,             // 结束时间
created_at: '2018-08-31 11:01:17',       // 生成时间
updated_at: '2018-08-31 11:01:22',
statusCN: '审核已通过'                  

}
}
},
mounted () {
this.getData()
},
methods: {
getData () {
let params = new URLSearchParams()
var self = this
params.append('bill_id', util.getQueryParam('id'))
var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('buyer/center/billInfo', params).then(res => {
if (res.data) {
console.log("???:",self.detail)
self.detail = res.data.data
}
})
},
dateFormat (date) {
return new Date(date * 1000).Format('yyyy-MM-dd hh:mm:ss') 
}
}
}



